@use 'sass:math';

// grid
@for $i from 1 through $col-count {
  .col-#{$i} {
    flex-basis: math.div(100, math.div($col-count, $i)) * 1%;
    // flex-basis: calc(100 / ($col-count / $i)) * 1%;
    padding: 0 $gutter-size;
  }
}

.row {
  box-sizing: border-box;
  display: flex;
  margin-left: -$gutter-size;
  margin-right: -$gutter-size;
}
